*, h1, h2, p {
  margin: 0;
  padding: 0;
}

body {
  padding-left: 40px;
  padding-right: 40px;
  overflow-x: hidden;
}

.select-container {
  margin-top: 20px;
}

.breed-select {
  width: 150px;
  margin-bottom: 10px;
  padding: 4px;
}

.box {
  text-align: center;
  color: teal;
  border: 1px solid teal;
  border-radius: 5px;
  flex-direction: column;
  gap: 20px;
  padding: 50px;
  display: flex;
}

.cat-info {
  gap: 30px;
